<template>
  <div>
    <bread :msg1="''" :msg2="''"></bread>
    <el-card class="box-card" :body-style="{ padding: '20px' }">
      <el-input placeholder="" value="" class="input-with-select">
        <el-button slot="append" icon="el-icon-search" @click="search"> </el-button>
      </el-input>
      <!--  -->
      <el-table :data="orderData" border style="width: 100%">
        <el-table-column type="index" label="#" width="50"></el-table-column>
        <el-table-column prop="order_number" label="" width="500"> </el-table-column>
        <el-table-column prop="order_price" label="()" width="100"> </el-table-column>
        <el-table-column prop="order_pay" label="" width="100">
          <template slot-scope="scoped">
            <el-button v-cloak size="mini" type="primary" v-if="scoped.row.order_pay - 0 === 1">
              
            </el-button>
            <el-button v-cloak size="mini" type="danger" v-else-if="scoped.row.order_pay - 0 === 0">
              
            </el-button>
            <el-button v-cloak size="mini" type="warning" v-else>
              
            </el-button>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label="" width="80"> </el-table-column>
        <el-table-column prop="add_time" label="" :formatter="formatter" width="200px"> </el-table-column>
        <el-table-column label="">
          <template slot-scope="cc">
            <el-button type="primary" icon="el-icon-edit" size="small" :aa=cc @click="dialogVisibleEdit = true"> </el-button>
            <el-button type="warning" icon="el-icon-location-outline" size="small" @click="getLogistics"> </el-button
          ></template>
        </el-table-column>
      </el-table>
      <!--  -->
      <div class="block">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="paramsObj.pagenum"
          :page-sizes="[5, 10, 15, 30, 50]"
          :page-size="paramsObj.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
      <!--  -->
      <el-dialog title="" :visible.sync="dialogVisibleEdit" width="30%" @close="closeDialog">
        <el-form ref="editForm" label-width="80px">
          <el-form-item label="/">
            <el-cascader style="width:100%" :options="cityOptions" :value="city" @change="changeProvince" :props="props" clearable> </el-cascader>
          </el-form-item>
          <el-form-item label="">
            <el-input value=""></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisibleEdit = false"> </el-button>
          <el-button type="primary" @click="dialogVisibleEdit = false"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog title="" :visible.sync="dialogVisible" width="50%">
        <div class="block">
          <el-timeline>
            <el-timeline-item placement="top" v-for="(item, index) in logistics" :key="index" :timestamp="item.time.split(' ')[0]">
              <el-card>
                <h4>{{ item.context }}</h4>
                <p style="color: #666; margin-top: 15px;">{{ item.time }}</p>
              </el-card>
            </el-timeline-item>
          </el-timeline>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="dialogVisible = false"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import cityOptions from '../../assets/js/city_data2017_element.js'
export default {
  data() {
    return {
      // 
      cityOptions: cityOptions,
      city: [],
      props: {
        expandTrigger: 'hover',
        label: 'label',
        children: 'children',
        value: 'value'
      },
      paramsObj: {
        pagenum: 1,
        pagesize: 10
      },
      total: 0,
      orderData: [],
      typeArr: [
        { typeBtn: 'danger', msg: '' },
        { typeBtn: 'primary', msg: '' }
      ],
      dialogVisibleEdit: false,
      dialogVisible: false,
      logistics: []
    }
  },
  methods: {
    // 
    async getOrderData() {
      const res = await this.$axios.get('/orders', { params: this.paramsObj })
      if (res.data.meta.status === 200) {
        this.orderData = res.data.data.goods
        this.total = res.data.data.total
      } else {
        this.$message = { type: 'error', message: res.data.meta.msg }
      }
    },
    // 
    search() {},
    // 
    handleSizeChange(e) {
      this.paramsObj.pagesize = e
      this.getOrderData()
    },
    handleCurrentChange(e) {
      this.paramsObj.pagenum = e
      this.getOrderData()
    },
    // 
    formatter(row, column) {
      const date = new Date(row.create_time)
      const y = date.getFullYear()
      const mon = this.addZero(date.getMonth() + 1)
      const day = this.addZero(date.getDate())
      const h = this.addZero(date.getHours())
      const m = this.addZero(date.getMinutes())
      const s = this.addZero(date.getSeconds())
      return y + '-' + mon + '-' + day + '  ' + h + ':' + m + ':' + s
    },
    addZero(a) {
      return a > 10 ? a : '0' + a
    },
    /*  */
    closeDialog() {
      this.$message({
        message: '' + this.city.join('-'),
        type: 'success'
      })
    },
    /*  */
    changeProvince(e) {
      this.city = e
    },
    async getLogistics() {
      const res = await this.$axios.get('/kuaidi/1106975712662')
      if (res?.data?.meta?.status === 200) {
        this.logistics = res.data.data
        this.dialogVisible = true
      } else {
        this.$message({ message: res?.data?.meta?.msg, type: 'error' })
      }
    }
  },
  created() {
    // 
    this.getOrderData()
  }
}
</script>

<style lang="less" scoped>
.el-input-group {
  width: 33% !important;
  margin-right: 20px;
}
.el-table {
  margin-top: 15px;
  /deep/.cell {
    font-size: 12px;
  }
  .el-table-column:nth-child(4) {
    .el-button {
      padding: 5px;
    }
  }
}
.el-pagination {
  margin-top: 15px;
}
</style>
